/*
  * appbar
*/
.appbar__ct {
  position: relative;
  width: auto;
  height: auto;
}
.appbar__bar {
  position: relative;
  width: 100%;
  height: 11vh;
  background-color: #fafafa;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  z-index: 49;
  color: #757575;
  font-weight: 400;
  font-family: "Montserrat", sans-serif;
}
.appbar__bar__left {
  position: absolute;
  width: auto;
  height: auto;
  left: 0;
  margin-left: 3%;
  font-weight: bolder;
  font-size: 1.35em;
}
.appbar__bar__title{
  color: #333;
}
.appbar__bar__right{
  width: auto;
  height: auto;
  position: absolute;
  right: 0;
  margin-right: 4%;
  font-size: 0.9em;
  -webkit-transform: translateY(0.5vh);
      -ms-transform: translateY(0.5vh);
          transform: translateY(0.5vh);
}
@media screen and (max-width: 430px) {
  .appbar__bar__left {
    margin-left: 7%;
    font-size: 1.15em;
  }
  .appbar__bar__right {
    margin-right: 7%;
    font-size: 1em;
  }
}


